@import url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/fonts/satoshi/stylesheet.css);
@font-face {
  font-family: "Material Symbols Outlined";
  font-style: normal;
  font-weight: 100 700;
  src: url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/gstatic/icons.woff2) format("woff2");
}
:root {
  color-scheme: dark;
}

.icons {
  font-family: "Material Symbols Outlined";
  font-weight: normal;
  font-style: normal;
  font-size: 1.25em;
  line-height: 0.79;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

* {
  margin: 0;
  padding: 0;
  font-family: satoshi-regular, arial, sans-serif;
  box-sizing: border-box;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
nav ul {
  display: flex;
  align-items: center;
  list-style: none;
  font-size: 1.125rem;
}
nav a {
  color: inherit;
  text-decoration: none;
}
nav li {
  height: 2.25rem;
  border-radius: 6px;
}
nav li a {
  display: flex;
  align-items: center;
  height: 2.25rem;
  padding: 0 1rem 0 1rem;
}
nav .menu {
  display: none;
}
nav .links {
  display: flex;
  flex-direction: row;
}
nav .links .dropdown {
  transition: background-color 250ms;
}
nav .links .dropdown > a .icons {
  font-size: inherit;
  rotate: -180deg;
  transition: rotate 250ms, transform 250ms;
}
nav .links .dropdown .spacer {
  width: 100%;
  height: 0.25rem;
}
nav .links .dropdown ul {
  background-color: rgba(29, 29, 33, 0.95);
  position: absolute;
  height: fit-content;
  width: max-content;
  border-radius: 6px;
  display: flex;
  align-items: start;
  flex-direction: column;
  gap: 0;
  opacity: 0;
  transform: translateY(1rem);
  pointer-events: none;
  transition: opacity 250ms, transform 250ms;
}
@supports (backdrop-filter: blur()) {
  nav .links .dropdown ul {
    background-color: rgba(29, 29, 33, 0.4);
    backdrop-filter: blur(0.5rem);
  }
}
nav .links .dropdown ul li {
  width: 100%;
  height: auto;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  transition: background-color 250ms;
}
nav .links .dropdown ul li .icons {
  margin-right: 0.75rem;
}
nav .links .dropdown ul li:hover {
  background-color: #1d1d21;
}
nav .links .dropdown:hover {
  background-color: #1d1d21;
}
nav .links .dropdown:hover > a .icons {
  rotate: 0deg;
  transform: translateY(1px);
}
nav .links .dropdown:hover ul {
  opacity: 1;
  transform: translateY(0);
  pointer-events: all;
}
nav .divider {
  display: none;
}
nav #nav-signup {
  background-color: #b7484f;
  border: transparent !important;
}
nav #nav-signup a {
  background-color: #e36970;
  border-radius: inherit;
  transition: transform 250ms;
}
nav #nav-signup:hover a {
  transform: translateY(-0.3rem);
}